/* Anchor link styling for headings */
.anchor {
  color: inherit;
  text-decoration: none;
  position: relative;
  display: inline-block;

  &::before {
    content: "#";
    color: var(--accent);
    font-weight: bold;
    position: absolute;
    right: -1.5rem;
    top: 0;
    opacity: 0;
    transition: opacity 0.2s ease-in-out;
  }
}

/* Show anchor on hover for the heading */
h1, h2, h3, h4, h5 {
  &:hover .anchor::before {
    opacity: 1;
  }

  /* Ensure proper positioning for anchor links */
  position: relative;

  /* Add some spacing for the anchor */
  padding-right: 1.5rem;
  margin-right: -1.5rem;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .anchor {
    margin-left: 0.25rem;

    &::before {
      right: -1rem;
    }
  }

  h1, h2, h3, h4, h5 {
    padding-right: 1rem;
    margin-right: -1rem;
  }
}
